.header{
    background-color: rgba(255, 255, 255, 0.833);
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 0.5rem 1rem;
    height: 74px;
    position: fixed;
    top: 10px;
    width: 100%;
    z-index: 100;
    box-shadow: 1px 1px 13px rgba(0, 0, 0, 0.412);
    border-radius: 10px;
}
.logo{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 0.8rem;
}
.logo>span{
    font-weight: 900;
    font-size:1.2rem;
}
.menu{
    flex: 2;
    display: flex;
    list-style: none;
    gap: 2rem;
    justify-content: center;
    z-index: 100;
 }

.menu>li a {
    text-decoration: none;
    color: black;
    padding: 5px 10px;
    border-radius: 5px;
}
.menu>li a:hover{
    color: white;
    background-color: #3052eb;
    cursor: pointer;
    transition: all 0.3s linear;
}
.rightSide{
    flex: 0.5;
    height: 50px;
    display: flex;
    gap: 1rem;
    align-items: center;
    justify-content:center;
}
.toggle{
    display: none;
}
.rightSide button{
    padding: 0.8rem;
    width: 120px;
    cursor: pointer;
    border: none;
    border-radius: 9px;
    color: white;
    background-color: #3052eb;
    text-transform: uppercase;
    font-size: 0.9rem;
}
.rightSide button:hover{
    background-color: #0b29c0;
}

/* Meadia Query */

@media screen and (min-width:280px) and (max-width:1080px) {
    .header{
        top: 0px;
    }
    .menu,.btn{
        display: none;
   }
    .toggle{
        display: block;
    }
}