
.mainimg img{
    width: 100%;
    height: 110vh;
    /* filter: brightness(65%); */
    box-shadow: 2px 2px 9px black;
}
section .content{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    left: 0;
    margin-top: 2rem;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    transition: all 0.5s linear;
}

.content .title{
    color: rgb(0, 0, 0);
}
.content h1{
    font-size: 3rem;
    letter-spacing: 0.2rem;
}
.content p{
    text-align: center;
    padding: 0 28vw;
    margin-top: 0.5rem;
    font-size: 1rem;
}
.content .search{
    display: flex;
    background-color: #ffffffce;
    padding: 0.5rem;
    margin-top: 2.5rem;
    border-radius: 0.5rem;
}
.content .search .container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 0 1.5rem;
}
.search .container label{
    font-size: 1.1rem;
    color: #03045e;
}
.search .container input{
    border: none;
    background-color: transparent;
    text-align: center;
    color: black;
    margin-top: 5px;
}
.search .container input:focus{
    outline: none;
}

.content button{
    padding: 1rem;
    cursor: pointer;
    border: none;
    border-radius: 9px;
    color: white;
    background-color: #3052eb;
    text-transform: uppercase;
    font-size: 0.9rem;
}
.content button:hover{
    background-color: #061873;
}


/* Media Query */

@media screen and (min-width:280px) and (max-width:980px) {
    .mainimg img{
        height:550px;
        width: 107%;
    }
    .content .title h1{
        font-size: 1.5rem;
    }
    .content p{
        font-size: 0.9rem;
        padding: 1vw;
    }
    .search{
        flex-direction: column;
        padding: 0.8rem;
        gap: 0.8rem;
    }
    .search .container{
        padding: 0 0.8rem;
    }
    button{
        padding: 1rem;
    } 
     

}