
#recommend{
    padding:2rem 0;
}
#recommend .title{
    text-align: center;    
}
#recommend .packeges{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 2rem 0;
}
.packeges ul{
    display: flex;
    width: max-content;
    list-style-type: none;
}
.packeges ul li{
    padding: 1rem 2rem;
    border-bottom: 0.3rem solid black;
    cursor: pointer;
}
.packeges ul li.active{
    border-bottom: 0.5rem solid #8338ce;
}

/* destinations */

.destinations{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 3rem;
    padding: 0 3rem;
}
.destinations .destination{
    background-color: rgba(185, 186, 189, 0.336);
    padding: 1rem;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    border-radius: 20px;
    cursor: pointer;
    transition: all 0.5s linear;
}
.destinations .destination img{
    width: 100%;
    border-radius: 10px;
}
.destination:hover{
    box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.19);
}

.destination:hover img[alt="Destination"]{
    transform: scale(110%,110%);
    transition: all 0.3s ease-in-out;
}
.destination .info {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.info h4 {
    color: rgb(23, 17, 147);
}

.destination .info .services{
    display: flex;
    align-items: center;
    gap: 0.3rem;
}
.destination .info .services img{
    background-color: #8338ce;
    width: 2rem;
    padding: 0.2rem 0.2rem;
    border-radius: 1rem;
}
.distance{
    display: flex;
    justify-content: space-between;
}

/* Media Query */

@media screen and (min-width:280px) and (max-width:720px) {
   #recommend{
     margin-left: 20px;
   }
   .packeges ul li{
        padding: 0 0.8rem;
        font-size: 2vh;
    }
    .destinations{
        grid-template-columns: 1fr;
        padding: 0;
    }
}