footer{
    display: flex;
    justify-content: space-evenly;
    /* align-items: center;/ */
    padding: 2rem;
    background-color: aliceblue;
}
footer ul{
    display: flex;
    list-style-type: none;
    gap: 2rem;
}
footer ul li a{
    text-decoration: none;
    color: black;
    transition: all 0.5s ease-in-out;
}
footer ul li a:hover{
    color: red;
}
/* icons */
svg{
    font-size: 1.5rem;
    text-shadow: 1px 1px 2px red;
}
svg:hover{
    color: blue; 
    cursor: pointer;
}
footer span{
    color: red;
}

/* Media Query */

@media screen and (min-width:280px) and (max-width:720px) {
footer{
    flex-direction: column;
    gap: 2rem;
    padding: 40px;
}
footer ul{ 
    flex-direction: column;
}
footer .social{ 
   flex-direction: row;
}
}
