.Construction{
    text-align: center;
    margin: 150px 120px;
    display: grid;
    justify-content: center;
    align-items: center;
}
.Construction > img{
    width: 300px;
    border-radius: 12px;
}
.Construction >p{
    color: gray;
    margin-top: 20px;
}
/* Places */
#text span{
    color: red;
}
.placedatacontainer{
    position: absolute;
    top: 89px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 10px;
    margin: 0px 120px;
    color: rgba(0, 0, 255, 0.835);
    text-shadow: 2px 2px 3px rgba(0, 0, 0, 0.226);
}
.placedatacontainer .placedata{
    width: 100%;
    height: 450px;
    border-radius: 20px;
    position: relative;
    overflow: hidden;
}
.placedatacontainer .placedata img{
    width: 100%;
    height: 100%;
    border-radius: 20px;
    object-fit: cover;
    cursor: pointer;
}
.placedatacontainer .placedata .placetitle{
    position: absolute;
    width: 100%;
    bottom: 20px;
    z-index: 5;
    font-size: 26px;
    color: yellow;
    text-shadow: 2px 2px 3px black;
    text-align: center;
}
.placedatacontainer .placedata .placetitle p{
    color: red;
    font-weight: 600;
}
.placedatacontainer .placedata img:hover{
    transform: scale(110%,110%);
    transition: all 0.3s linear;
}

/* Media query */

@media screen and (min-width:280px) and (max-width:720px) {
    .placedatacontainer{
        margin: 0 12px;
    }
    .placedatacontainer .placedata{
        width: 100%;
        height: 80%;
    }
    .placedatacontainer .placedata img{
        width: 100%;
        height: 80%;
    }
    .placedatacontainer .placedata .placetitle{
        font-size: 12px;
        bottom: 22px;
    }
    #text{
        font-size: 18px;
    }
   
 }