.item{
    height: 25em;
    min-width: 35rem;
    padding: 10px;
    position: relative;
}
.item img{
    width:100%;
    height: 95%;
    border-radius: 1rem;
    pointer-events: none;
}
.item h2{
    position: absolute;
    bottom: 63px;
    left: 39px;
    font-size: 26px;
    color: yellow;
    text-shadow: 2px 2px 3px black;
}

.innerCarousal{
    display: flex;
    /* background-color: red; */
}

.carousel{
    cursor: grab;
    margin: 0px 1rem; 
    overflow: hidden;
    /* background-color: rgba(0, 0, 0); */
}
.places{
    text-align: center;
    margin: 1rem 0;
}

/* Media  Query*/
@media screen and (min-width:280px) and (max-width:720px) {

.item{
    padding: 20px;
    min-width: 20rem;
    border-radius: 1rem;
}
.item img{
    width:110%;
    height: 80%;
}
.item h2{
    font-size: 21px;
    bottom: 117px;
}
}

@media screen and (min-width:720px) and (max-width:1080px) {
    .item{
        padding: 15px;
        min-width: 30rem;
    }
}