.container{
    margin: 5rem 0;
    transition: all 0.5s linear;
}
.container .title{
    text-align: center;
    margin-bottom: 2rem;
}
.container .testimonials{
    display: flex;
    justify-content: center;
    margin: 0 2rem;
    gap: 2rem;
}
.testimonial{
    background-color: aliceblue;
    padding: 2rem;
    border-radius: 15px;
    box-shadow:1px 1px 10px rgba(0, 0, 0, 0.301) ;
    transition: all 0.3s ease-in-out;
}
.testimonial:hover{
    transform: translate(0.4rem,-1rem);
}
.testimonial .info {
    display: flex;
    justify-content: center;
    gap: 1rem;
    align-items: center;
    margin-top: 1rem;
}
.testimonial .info img{
    height: 3rem;
    clip-path: circle(35%);
} 
.testimonial .info .details span{
    font-size: 0.9rem;
}

/* Media Query */

@media screen and (min-width:280px) and (max-width:720px) {
   .testimonials{
    flex-direction: column;
   }
}