nav {
  display: flex;
  justify-content: space-evenly;
  padding: 2rem;
  width: 100%;
  background-color: aliceblue;
  position: fixed;
  top: 74px;
}
nav ul {
  display: flex;
  list-style-type: none;
  gap: 2rem;
}
nav ul li a {
  text-decoration: none;
  color: black;
  transition: all 0.5s ease-in-out;
}
nav ul li a:hover {
  color: red;
}
/* icons */
svg {
  font-size: 1.5rem;
  text-shadow: 1px 1px 2px red;
}
svg:hover {
  color: blue;
  cursor: pointer;
}
nav span {
  color: red;
}

/* Media Query */

@media screen and (min-width: 280px) and (max-width: 1025px) {
  nav {
    flex-direction: column;
    gap: 2rem;
    z-index: 1000;
  }
  nav ul {
    flex-direction: column;
  }
  nav .social {
    flex-direction: row;
  }
}
